<template><section>
<h1>CysFormSearch 表单</h1>
<h2 id="%E5%9F%BA%E7%A1%80%E7%94%A8%E6%B3%95">基础用法 <a class="cys-markdown-permalink" href="#%E5%9F%BA%E7%A1%80%E7%94%A8%E6%B3%95">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <div>
    <cys-form-search :props="{ showRest: true }" :data="form.data" :btns="form.btns" :direction="'vertical'"></cys-form-search>
  </div>
</template>


</div>
                            <p>通过<code>plain</code>属性可以设置为朴素的按钮</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-form-search</span>
      <span class="hljs-attr">:props</span>=<span class="hljs-string">"{ showRest: true }"</span>
      <span class="hljs-attr">:data</span>=<span class="hljs-string">"form.data"</span>
      <span class="hljs-attr">:btns</span>=<span class="hljs-string">"form.btns"</span>
      <span class="hljs-attr">:direction</span>=<span class="hljs-string">"'vertical'"</span>
    &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-form-search</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
