    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">form</span>: {
          <span class="hljs-attr">btns</span>: [
            {
              <span class="hljs-attr">label</span>: <span class="hljs-string">"查询"</span>,
              <span class="hljs-attr">action</span>: <span class="hljs-string">"search"</span>
            }
          ],
          <span class="hljs-attr">data</span>: [
            {
              <span class="hljs-attr">type</span>: <span class="hljs-string">"input"</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">"输&amp;nbsp;&amp;nbsp;入&amp;nbsp;&amp;nbsp;框"</span>,
              <span class="hljs-attr">field</span>: <span class="hljs-string">"ipu"</span>,
              <span class="hljs-attr">value</span>: <span class="hljs-string">""</span>,
              <span class="hljs-attr">placeholder</span>: <span class="hljs-string">"全部"</span>,
            },
            {
              <span class="hljs-attr">type</span>: <span class="hljs-string">"select"</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">"收件方式"</span>,
              <span class="hljs-attr">field</span>: <span class="hljs-string">"receiptType"</span>,
              <span class="hljs-attr">value</span>: <span class="hljs-string">""</span>,
              <span class="hljs-attr">placeholder</span>: <span class="hljs-string">"全部"</span>,
              <span class="hljs-attr">options</span>: [
                {
                  <span class="hljs-attr">label</span>: <span class="hljs-string">"邮寄"</span>,
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"1"</span>
                },
                {
                  <span class="hljs-attr">label</span>: <span class="hljs-string">"电子邮箱"</span>,
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"2"</span>
                },
                {
                  <span class="hljs-attr">label</span>: <span class="hljs-string">"自取"</span>,
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"3"</span>
                }
              ],
              <span class="hljs-attr">filter</span>: <span class="hljs-literal">true</span>,
              <span class="hljs-attr">clearable</span>: <span class="hljs-literal">true</span>
            }
            {
              <span class="hljs-attr">type</span>: <span class="hljs-string">"daterange"</span>,
