              <span class="hljs-attr">field</span>: <span class="hljs-string">"date"</span>,
              <span class="hljs-attr">value</span>: [],
              <span class="hljs-attr">placeholder</span>: <span class="hljs-string">"申请时间"</span>,
              <span class="hljs-attr">clearable</span>: <span class="hljs-literal">true</span>,
