</div></demo-block>
<h2 id="cysformsearch-attributes">cysFormSearch Attributes <a class="cys-markdown-permalink" href="#cysformsearch-attributes">#</a></h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>btns</td>
<td>按钮定义</td>
<td>array</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>data</td>
<td>表单元素</td>
<td>array</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>direction</td>
<td>排列方向</td>
<td>string</td>
<td>vertical,horizontal</td>
<td>horizontal</td>
</tr>
<tr>
<td>pointer</td>
<td>绑定表格组件ref的名称，如在表格组件上加ref=&quot;cys-ref-table&quot;,就能让表单搜索关联表格组件</td>
<td>string</td>
<td>-</td>
<td>cys-ref-table</td>
</tr>
<tr>
<td>parent</td>
<td>表单父层组件绑定, 例如在想要父组件加上componentName: &quot;pm&quot;</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2 id="btns-%E6%95%B0%E7%BB%84%E9%85%8D%E7%BD%AE%E8%AF%B4%E6%98%8E">btns 数组配置说明 <a class="cys-markdown-permalink" href="#btns-%E6%95%B0%E7%BB%84%E9%85%8D%E7%BD%AE%E8%AF%B4%E6%98%8E">#</a></h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>按钮名称</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>action</td>
<td>点击按钮响应方法，且返回表单元素返回值</td>
<td>funciton</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2 id="data-%E6%95%B0%E7%BB%84%E9%85%8D%E7%BD%AE%E8%AF%B4%E6%98%8E">data 数组配置说明 <a class="cys-markdown-permalink" href="#data-%E6%95%B0%E7%BB%84%E9%85%8D%E7%BD%AE%E8%AF%B4%E6%98%8E">#</a></h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>类型</td>
<td>input,select,daterange,date,cascader</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>field</td>
<td>表单元素绑定搜索字段名称</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>label</td>
<td>表单元素label名称</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>value</td>
<td>表单元绑定值</td>
<td>string,array</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>placeholder</td>
<td>表单元默认值</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>clearable</td>
<td>表单元素是否可清除</td>
<td>boolean</td>
<td>true,false</td>
<td>false</td>
</tr>
<tr>
<td>options</td>
<td>在类型为select,cascader使用，下拉框默认选项</td>
<td>array</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>filter</td>
<td>在类型为cascader使用,是否可搜索</td>
<td>boolean</td>
<td>true,false</td>
<td>false</td>
</tr>
<tr>
<td>interface</td>
<td>在类型为select,cascader使用,接收一个后台接口请求</td>
<td>function</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>cKey</td>
<td>在类型为select,cascader使用,接收一个后台接口请求返回值code字段名配置</td>
<td>string</td>
<td>-</td>
<td>errorCode</td>
</tr>
<tr>
<td>cVal</td>
<td>在类型为select,cascader使用,接收一个后台接口请求返回值code字段值配置</td>
<td>string,number</td>
<td>-</td>
<td>&quot;0&quot;</td>
</tr>
<tr>
<td>dKey</td>
<td>在类型为select,cascader使用,接收一个后台接口请求返回对象名配置</td>
<td>string</td>
<td>-</td>
<td>data</td>
</tr>
<tr>
<td>optionLabel</td>
<td>在类型为select使用,配置option数组对象里的绑定的对象名称</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>optionValue</td>
<td>在类型为select使用,配置option数组对象里的绑定的对象值</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>cashName</td>
<td>在类型为select并且为interface接口使用, 配一个下拉元素字段值做为另一个下拉元素的拉取接口条件</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>arr2Obj</td>
<td>在类型为daterange使用,将日期控件返回数组值转换为表单想要的字段值</td>
<td>object</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>placeholder</td>
<td>表单元默认值</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  module.exports = {
    componentName: "formexmple",
    data() {
      return {
        form: {
          btns: [
            {
              label: "查询",
              action: this.search
            }
          ],
          data: [
            {
              type: "input",
              label: "申请时间",
              field: "ipu",
              value: "",
              placeholder: "全部",
              clearable: true
            },
            {
              type: "cascader",
              label: "日期",
              field: "ipu2",
              value: "",
              options: [
                {
                  label: "一级菜单",
                  value: 1,
                  children: [
                    {
                      label: "二级菜单1",
                      value: 2,
                      children: [
                        {
                          label: "三年级菜单1",
                          value: 3
                        },
                        {
                          label: "三年级菜单2",
                          value: 5
                        }
                      ]
                    },
                    {
                      label: "二级菜单2",
                      value: 4
                    }
                  ]
                },
                {
                  label: "指南",
                  value: 6,
                  children: [
                    {
                      label: "指南2",
                      value: 7
                    }
                  ]
                }
              ],
              placeholder: "全部",
              clearable: true
            },
            {
              type: "select",
              label: "收件方式",
              field: "receiptType",
              value: "",
              placeholder: "全部",
              options: [],
              filter: true,
              clearable: true
            },
            {
              type: "daterange",
              label: "申请时间",
              field: "date",
              value: [],
              placeholder: "申请时间",
              clearable: true,
              arr2Obj: {
                startTime: "",
                endTime: ""
              }
            }
          ]
        }
      };
    },
    created() {
      setTimeout(() => {
        this.form.data[2].options = [
                {
                  label: "邮寄",
                  value: "1"
                },
                {
                  label: "电子邮箱",
                  value: "2"
                },
                {
                  label: "自取",
                  value: "3"
                },
                {
                  label: "邮寄",
                  value: "1"
                },
                {
                  label: "电子邮箱",
                  value: "2"
                },
                {
                  label: "自取",
                  value: "3"
                },
                {
                  label: "邮寄",
                  value: "1"
                },
                {
                  label: "电子邮箱",
                  value: "2"
                },
                {
                  label: "自取",
                  value: "3"
                }
              ]
      }, 1000)
    },
    methods: {
      search(val) {
        console.log(val, '==');
      }
    }
  };
</script>


